import "./style/Super8.scss"
import Grid from '@mui/material/Grid';
import { Container } from "@mui/system";
import {Link} from 'react-scroll'

const Super8 = () => {
 
    return (
        <div  className="super8Background">
            <div  id="super8" >
                    <h1 className="tituloSeccion">SPER 8MM</h1>
                    <video src="./super8_x264.mp4" width="100%" height="auto" controls allowFullScreen="allowFullScreen" preload="false"></video>
                   {/*  <iframe src="https://drive.google.com/file/d/1hCLn7A7WrOOpIy3hBoUJO-AQOX-FNswq/preview" width="427" height="240" preload="false" allowfullscreen="allowfullscreen" controls></iframe> */}
                    <div>
                        El Super 8mm es un formato de pelcula de cine que antiguamente se usaba en los paseos familiares, cumpleaos, bodas y otros eventos. Luego de filmar los familiares, se enviaba a laboratorios para su posterior revelado. Al ser revelado, este se poda proyectar en el living de tu casa. Pero para todo ello, es de completa necesidad adquirir cmaras que hacen funcionar este material sensible. Y as, cada elemento dentro crea un recuerdo y las primeras formas de cine domstico.
                        <br />
                        Detalles generales: A continuacin se enlistan los procesos de servicio que Poacea Estudio entrega.
                        
                        Detalles especficos:
                        <ul>
                        <li>Reel del formato: Se muestra el video explicativo del servicio Poacea en formato Super 8mm.</li>
                        <li>Proceso de Filmacin: Filmacin del evento. Para ello, se usa la cmara filmadora de Super 8mm.</li>
                        <li>Proceso de Revelado: Consta de enrollar la o las pelculas filmadas en un tanque de revelado especial para la cantidad de metros de cada cartucho de Super 8mm (15 metros x cada cartucho).</li>
                        <li>Proceso de Digitalizacin: Al igual que en el proceso de revelado, es necesario que la pelcula de 15 metros pase por un scanner especfico para el formato Super 8mm.
                            Proceso de entrega: Para la elaboracin de cada pieza, se entrega un carrete con la pelcula en su respectiva caja.</li>
                        </ul>
                    </div>
                    
                    <Link to="contacto" spy={true} smooth={true} duration={500} className="contactButton super8Buttton">Contacto</Link>
                
            </div>
        </div>
    )

}

export default Super8